import { createContext, FC, ReactNode, useContext } from "react";
import { useGenAILive, UseGenAILiveResults } from "../hooks/use-genai-live";

const GenAILiveContext = createContext<UseGenAILiveResults | undefined>(
  undefined
);

export type GenAILiveProviderProps = {
  children: ReactNode;
  apiKey: string;
};

export const GenAILiveProvider: FC<GenAILiveProviderProps> = ({
  apiKey,
  children,
}) => {
  const genAILive = useGenAILive({ apiKey });

  return (
    <GenAILiveContext.Provider value={genAILive}>
      {children}
    </GenAILiveContext.Provider>
  );
};

export const useGenAILiveContext = () => {
  const context = useContext(GenAILiveContext);
  if (!context) {
    throw new Error(
      "useGenAILiveContext must be used within a GenAILiveProvider"
    );
  }
  return context;
};
